import React, { Component } from "react";
import classnames from "classnames";
import windowDimensions from "react-window-dimensions";

import "./App.css";
import Cover from "./components/cover";
import Registration from "./components/registration/Registration";
import { Row, Container, Alert } from "reactstrap";
import { Timeline, TimelineEvent } from "react-event-timeline";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      submitted: false
    };
  }
  render() {
    return (
      <div>
        <Cover>
          <h4>Welcome to hackathon !!!</h4>
          <h2>
            <a
              href="#register"
              className={classnames("bd-highlight", "text-monospace", "m-10")}
            >
              Let's Register
            </a>
          </h2>
          <h6>
            <a href="#timeline"> The format </a> | <a href="#faq"> FAQ </a>
          </h6>
          <h6>
            Looking for problem statements... stay tuned we will update you
          </h6>
        </Cover>

        <Container id="register">
          <div
            className={classnames("row align-items-center")}
            style={{ height: this.props.height }}
          >
            {this.state.submitted && (
              <Alert color="success">
                Your registration is successful. Thank you for registration. See
                you on 29th June in Megaplex at 7 PM.
              </Alert>
            )}
            {!this.state.submitted && (
              <Registration
                onSubmit={() => this.setState({ submitted: true })}
              />
            )}
          </div>
        </Container>
        <Container id="timeline" className={classnames("mt-4")}>
          <div className={classnames("row align-items-center")}>
            <h3> Timeline </h3>
            <Timeline>
              <TimelineEvent
                title="Opening"
                createdAt="2018-06-29 07:00 PM"
                icon={<i />}
                iconColor="#6fba1c"
              >
                We will be starting at 7 PM. Our architects will address the
                audiance. Explain the concept and will walk over the format.
              </TimelineEvent>
              <TimelineEvent
                title="Setup for team's/individual's idea"
                createdAt="2018-06-29 07:30 PM"
                icon={<i />}
                iconColor="#03a9f4"
              >
                We will meet the teams and individuals to finalise on the idea
                that they plan to build in next 12 hours. We will setup the
                expectations/outputs for every one.
              </TimelineEvent>
              <TimelineEvent
                title="Dinner Time"
                createdAt="2018-06-29 08:30 PM"
                icon={<i />}
                iconColor="#03a9f4"
              >
                Before getting started with building we will get done with
                dinner.
              </TimelineEvent>
              <TimelineEvent
                title="Hacking Starts"
                createdAt="2018-06-29 09:30 PM"
                icon={<i />}
                iconColor="#03a9f4"
              >
                We start building.
              </TimelineEvent>
              <TimelineEvent
                title="Routine update check"
                createdAt="2018-06-29 11:30 PM"
                icon={<i />}
                iconColor="#03a9f4"
              >
                We will reach out to individuals/ teams to see if they have any
                problems. If any one needs help. How they are progressing.
              </TimelineEvent>
              <TimelineEvent
                title="Routine update check"
                createdAt="2018-06-30 1:30 AM"
                icon={<i />}
                iconColor="#03a9f4"
              >
                <p>Tea / Coffee / Snacks will be available .</p>
                <p> You also can breakout for sometime. </p>
                <p>
                  We will reach out to individuals/ teams to see if they have
                  any problems. If any one needs help. How they are progressing.
                </p>
              </TimelineEvent>
              <TimelineEvent
                title="Routine update check"
                createdAt="2018-06-30 3:30 AM"
                icon={<i />}
                iconColor="#03a9f4"
              >
                <p>Tea / Coffee / Snacks will be available .</p>
                <p> You also can breakout for sometime. </p>
                <p>
                  We will reach out to individuals/ teams to see if they have
                  any problems. If any one needs help. How they are progressing.
                </p>
              </TimelineEvent>
              <TimelineEvent
                title="Routine update check"
                createdAt="2018-06-30 5:30 AM"
                icon={<i />}
                iconColor="#03a9f4"
              >
                <p>Tea / Coffee / Snacks will be available .</p>
                <p> You also can breakout for sometime. </p>
                <p>
                  We will reach out to individuals/ teams to see if they have
                  any problems. If any one needs help. How they are progressing.
                </p>
              </TimelineEvent>
              <TimelineEvent
                title="Routine update check"
                createdAt="2018-06-30 7:30 AM"
                icon={<i />}
                iconColor="#03a9f4"
              >
                <p>Tea / Coffee / Snacks will be available .</p>
                <p> You also can breakout for sometime. </p>
                <p>
                  We will reach out to individuals/ teams to see if they have
                  any problems. If any one needs help. How they are progressing.
                </p>
              </TimelineEvent>
              <TimelineEvent
                title="Conclude hacking"
                createdAt="2018-06-30 09:30 AM"
                icon={<i />}
                iconColor="#03a9f4"
              >
                <p>
                  We will officially close the building process. By this time
                  you should have checked in your changes. You can continue
                  building, though anything after this will not be counted for
                  evaluation.
                </p>
                <p>Breakfast will be served</p>
              </TimelineEvent>
              <TimelineEvent
                title="Evaluation begins"
                createdAt="2018-06-30 10:00 AM"
                icon={<i />}
                iconColor="#03a9f4"
              >
                <p>
                  We will start the evvaluation for team/individuals against the
                  outcome they had mentioned in begining.
                </p>
              </TimelineEvent>
              <TimelineEvent
                title="Evaluation concludes"
                createdAt="2018-06-30 11:30 AM"
                icon={<i />}
                iconColor="#03a9f4"
              >
                <p>We will complete the evaluation by this time</p>
                <p>Brunch will be served by this time</p>
              </TimelineEvent>
              <TimelineEvent
                title="Announcements of the results"
                createdAt="2018-06-30 12:00 PM"
                icon={<i />}
                iconColor="#03a9f4"
              >
                <p>
                  We will begin the announcement of winners and ask them to talk
                  about the solution
                </p>
              </TimelineEvent>
              <TimelineEvent
                title="We windup the hackathon by this time"
                createdAt="2018-06-30 12:30 PM"
                icon={<i />}
                iconColor="#03a9f4"
              />
            </Timeline>
          </div>
        </Container>

        <Container id="faq" className={classnames("mt-4")}>
          <Row>
            <h3> FAQ </h3>

            <Row>
              <ol>
                <li>
                  <p> Will we get a list of problems to solve?</p>
                  <p>Yes. We will be sharing it soon</p>
                </li>
                <li>
                  <p> Will it require to be done in a specific language?</p>
                  <p>No. Any language, any techstack</p>
                </li>
                <li>
                  <p>
                    Can I pick a problem from my product and we as a team solve
                    it?
                  </p>
                  <p>
                    Yes,its open though you need a nod from your manager saying
                    it does not affect the client. You can just ask hime to send
                    <a href="mailto:bharat.mhaskar@talentica.com"> us</a> an
                    email saying the project you build in hackathon does not
                    impact client.
                  </p>
                </li>
                <li>
                  <p>
                    Will there be accounts provided for services that I would
                    like to use in building project. E.g. AWS, Auth0, Firebase
                  </p>
                  <p>
                    No. You could use your talentica email for registring to opt
                    in for free tier options provided by services you would like
                    to use.
                  </p>
                </li>
                <li>
                  <p>Can I bring my code?</p>
                  <p>
                    No. You could use any existing open sourced libraries,
                    packages. So if you have open souced a library you can use
                    it.
                  </p>
                </li>
                <li>
                  <p>How much time I get to code?</p>
                  <p>
                    Around 12 hours to code. You can check the timeline
                    <a href="#timeline"> here</a>.
                  </p>
                </li>
                <li>
                  <p>What happens to the code that is written?</p>
                  <p>
                    It will be pushed to the gitlab repo provide by us. Other
                    related resources that you have used could be teared down,
                    though its up to you if you wish to maintain them.
                  </p>
                </li>
                <li>
                  <p>How are the solutions evaluated? </p>
                  <p>
                    We are having three criterias 1. Functional 2. Scalable 3.
                    Extensibility / Reusability We will define those for each
                    problem.(either specified by us or brought by you) Each
                    criteria has points and those will be sumed up to give you
                    total points for your solution. We compare the totals among
                    teams / individuals and decide the ultimate solution.
                  </p>
                </li>
                <li>
                  <p>Who will evaluate solutions? </p>
                  <p>Some of our architects will.</p>
                </li>
                <li>
                  <p>When will be the evaluation result announced? </p>
                  <p>At the end of the event.</p>
                </li>
                <li>
                  <p>What does ultimate solution builders get? </p>
                  <p>
                    Some goodies those are sceret ... come and see for yourself
                  </p>
                </li>
                <li>
                  <p>What do I need to follow?</p>
                  <p>A code of conduct.</p>
                  <p>
                    CODE OF CONDUCT: Harassment includes offensive verbal
                    comments related to gender, gender identity and expression,
                    age, sexual orientation, disability, physical appearance,
                    body size, race, ethnicity, nationality, religion, sexual
                    images in public spaces, deliberate intimidation, stalking,
                    following, photography or audio/video recording against
                    reasonable consent, sustained disruption of talks or other
                    events, inappropriate physical contact, and unwelcome sexual
                    attention. Photography is encouraged, but other participants
                    must be given a reasonable chance to opt out from being
                    photographed. If they object to the taking of their
                    photograph, comply with their request. It is inappropriate
                    to take photographs in contexts where people have a
                    reasonable expectation of privacy (in bathrooms or where
                    participants are sleeping). Participants asked to stop any
                    harassing behavior are expected to comply immediately. As
                    this is a Hackathon we like to explicitly note that the
                    hacks created at our Hackathon are equally subject to the
                    anti-harassment policy. Sponsors and partners are also
                    subject to the anti-harassment policy. In particular,
                    sponsors should not use sexualised images, activities, or
                    other material. Sponsor representatives (including
                    volunteers) should not use sexualised
                    clothing/uniforms/costumes, or otherwise create a sexualised
                    environment. If you are being harassed, notice that someone
                    else is being harassed, or have any other concerns, please
                    contact a member of hackathon staff immediately. Hackathon
                    staff will be happy to help participants contact any local
                    security or local law enforcement, provide escorts, or
                    otherwise assist those experiencing harassment to feel safe
                    for the duration of the hackathon. We value your attendance.
                    If a participant engages in harassing behavior, the
                    Hackathon organizers may take any action they deem
                    appropriate, including warning the offender or expulsion
                    from the Hackathon with no refund (if applicable). We expect
                    participants to follow these rules at Hackathon and workshop
                    venues and Hackathon-related social events
                  </p>
                </li>
              </ol>
            </Row>
          </Row>
        </Container>
      </div>
    );
  }
}
export default windowDimensions()(App);



